import uniPopup from "@/components/uni-popup/uni-popup.vue"
	export default {
		components: {
			uniPopup
		},
		onLoad() {
			this.initRecharge()
		},
		data () {
			return {
				//圆
				isChoice: false,
				//充值金额信息
				rechargeList:[],
				//是否选中充值金额
				currentIndex: -1,
				//其他金额
				elsePrice:' ',
				//支付宝还是微信
				circleTapIndex: 1,
				popupflag: 0,
				//保存选中的充值信息
				isChoiceItem: {}
			}
		},
		methods: {
			//选中
			isReader () {
				this.isChoice = !this.isChoice
			},
			//选中添加样式
			addActive (item,index) {
				if (this.currentIndex != index) {
					this.currentIndex = index
					this.isChoiceItem = item
				} else {
					this.currentIndex = -1
				}
				
			},
			//确定
			confirm () {
				if (!this.isChoice) {
					uni.showToast({
						icon:'none',
						title: '请认真阅读充值协议'
					})
					return
				} else {
					this.popupflag = 1
					this.$nextTick(function(){
						this.$refs.popup.open()
					})
				}
			},
			//充值
			realRecharge () {
				this.popupflag = 2
				this.$refs.popups.open()
			},
			//小圆点切换
			circleTap (number) {
				this.circleTapIndex = number
				
			},
			change (e) {
				
			},
			
			async initRecharge(){
				let res = await this.$fetch(this.$api.rechargeInfo,{},"POST","FORM")
				console.log(res)
				if(res.code == 0) {
					this.rechargeList = res.rows
				}
			},
			//其他金额提示
			showTip () {
				if (this.currentIndex != -1) {
					uni.showToast({
						icon: 'none',
						title: '您已选择固定充值金额，请取消后在点击'
					})
				}
			
			},
			//充值
			async rechargeSubmit () {
				if (this.currentIndex != -1) {
					let res = await this.$fetch(this.$api.userRecharge,{money:this.isChoiceItem.amount,payType:this.circleTapIndex},"Post","Form")
					console.log(this.isChoiceItem.amount)
					console.log(this.circleTapIndex)
					console.log(res)
					
					if (this.circleTapIndex === 1) {
						uni.requestPayment({
						    provider: 'alipay',
						    orderInfo: res.msg, //微信、支付宝订单数据
						    success: function (res) {
						        console.log('success:' + JSON.stringify(res));
								uni.showToast({
									icon:'success',
									title: '支付成功'
								})
								setTimeout(()=>{
									uni.navigateBack({
										delta:1
									})
								},1000)
						    },
						    fail: function (err) {
						        
						    }
						});
					}
					if (this.circleTapIndex === 2) {
						uni.requestPayment({
						    provider: 'wxpay',
						    orderInfo: res.data, //微信、支付宝订单数据
						    success: function (res) {
						        console.log('success:' + JSON.stringify(res));
								uni.showToast({
									icon:'success',
									title: '支付成功'
								})
								setTimeout(()=>{
									uni.navigateBack({
										delta:1
									})
								},1000)
								
						    },
						    fail: function (err) {
						        
						    }
						});
					}
					
					
				} else {
					let res = await this.$fetch(this.$api.userRecharge,{money:this.elsePrice,payType:this.circleTapIndex},"Post","Form")
					console.log(res)
					if (this.circleTapIndex === 1) {
						uni.requestPayment({
						    provider: 'alipay',
						    orderInfo: res.msg, //微信、支付宝订单数据
						    success: function (res) {
						        console.log('success:' + JSON.stringify(res));
								uni.showToast({
									icon:'success',
									title: '支付成功'
								})
								setTimeout(()=>{
									uni.navigateBack({
										delta:1
									})
								},1000)
						    },
						    fail: function (err) {
						        console.log('fail:' + JSON.stringify(err));
						    }
						});
					}
					if (this.circleTapIndex === 2) {
						uni.requestPayment({
						    provider: 'wxpay',
						    orderInfo: res.data, //微信、支付宝订单数据
						    success: function (res) {
						        console.log('success:' + JSON.stringify(res));
								uni.showToast({
									icon:'success',
									title: '支付成功'
								})
								setTimeout(()=>{
									uni.navigateBack({
										delta:1
									})
								},1000)
						    },
						    fail: function (err) {
						        console.log('fail:' + JSON.stringify(err));
						    }
						});
					}
				}
			},
			goToRichText (id) {
				uni.navigateTo({
					url: '../rich-text/rich-text?id=' + id
				})
			}
		}
	}
